.form{
    height: 200px;
    width: 400px;
    display: table;
    margin-top: 10%;
    margin-left: auto;
    margin-right: auto;
    background-color: rgba(0,0,0,.1);
    padding: 5px;
    border-radius: 5px;
}
.form> #title{
    background: blue;
    padding: 10px;
    color: #fff;
    font-weight: bold;
    font-size: 20px;
    text-align: center;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
    text-shadow: -1px -1px 0 rgba(0,0,0,.2);
}
.form > .body{
    background: #FFFFFF;
    padding: 10px;
    display: table;
}

input[type='text'],input[type='password'] {
    height: 35px;
    width: 100%;
    margin-bottom: 10px;
    box-sizing: border-box;
    padding: 4px 5px;
    background: #fff;
    border: 1px solid #d0d0d0;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
}
.loginBtn{
    height: 35px;
    box-sizing: border-box;
    padding: 4px 10px;
    background: #f5f4ea;
    border: 1px solid #d0d0d0;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    cursor: pointer;
}
.loginBtn:hover{
    background: #c3c3ba;
}
.registerBtn{
    float: right;
    background: blue;
    color: #fff;
    height: 35px;
    box-sizing: border-box;
    padding: 4px 10px;
    border: 1px solid #d0d0d0;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    cursor: pointer;
    margin-top: -35px;
}
.registerBtn:hover{
    background: blue;
}
.Error{
    padding: 10px;
    background-color: #feeaea;
    border: 1px solid #fadadb;
    margin-bottom: 10px;
    border-radius: 3px;
}